body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


body {
  background-color: #f0f0f0;
}

ul {
  padding: 0 0 0.7em 0;
}

li {
  list-style-type: none;
  width: 100%;
}

.input-label {
  font-size: 0.7em;
  font-style: italic;
  text-align: left;
  width: 100%;
  min-height: 1.8em;
  line-height: 0.9em;
  padding-left: 10px;
}

input, select {
  font-size: 0.8em;
}

input[type=checkbox] {
  width: 50px;
  float: left;
  font-size: 0.7em;
  margin-top: 1.2em;
}

input[type=submit] {
  display: none;
}

label {
  border-right: 1px solid #999;
  border-left: 1px solid #999;
  border-bottom: 1px solid #999;
  border-radius: 0 0 4px 4px;
  padding-right: 10px;
  padding-left: 10px;
  font-weight: bold;
  background-color: #fff;
  color: #666
}

.show {
  visibility: visible;
  opacity: 1;
  -webkit-transition: opacity 500ms linear;
  transition: opacity 500ms linear;
}
.hide {
  visibility: hidden;
  opacity: 0;
  -webkit-transition: opacity 500ms linear;
  transition: opacity 500ms linear;
}

select:required:invalid {
  color: gray;
}
option[value=""][disabled] {
  display: none;
}
option {
  color: black;
}

div.label {
  float:left;
  width:70%;
  text-align: left;
  border-bottom: 1px solid #dfdfdf
}

div.value {
  float:left;
  width:30%;
  text-align: right;
  border-bottom: 1px solid #dfdfdf
}

div.column {
  font-size: 0.9em;
  float:left;
  text-align: left;
  width: 36em;
  padding: 0.5em
}

div.res-block {
  display: inline-block;
  width: 100%;
  margin-bottom: 0.5em;
  border: 1px solid #ccc;
  padding: 0.5em;
  border-radius: 4px;
  background-color: #f8f8f8;
}

div.results {
  width: 36em
}

form>ul {
  float: left;
  width: 50%;
  padding: 0;
  margin: 0;
}

ul.left {
  padding-right: 0.2em;
}

ul.right {
  padding-left: 0.2em;
}

.checkbox-label {
  min-height: 2.6em;
  margin-bottom: 1em
}

.checkbox-label>label {
  border: none;
  margin-top: 1em;
  font-weight: normal;
  font-size: 1.1em;
  color: #666;
  background-color: transparent;
}

